import { Component, OnInit } from '@angular/core';
import { getStyle, hexToRgba } from '@coreui/coreui/dist/js/coreui-utilities';
import { CustomTooltips } from '@coreui/coreui-plugin-chartjs-custom-tooltips';
import { Router } from '@angular/router';
import { StationService } from '../../../services/station.service';
import * as Leaflet from 'leaflet';
import { AuthService } from '../../../services/auth.service';

@Component({
  templateUrl: 'dashboard.component.html',
  styleUrls: ['./dashbord.component.scss']
})
export class DashboardComponent implements OnInit {

  public map: Leaflet.Map;


  public stationList: any
  public setStation: any
  public latitude: any;
  public longitude: any;
  public alt: string;

  public adminCountAll: number = 0;
  public userCountAll: number = 0;
  public synopticCount: number;
  public hydrologyCount: number;





  constructor(
    private router: Router,
    private stationservice: StationService,
    public authService: AuthService,


  ) {

  }


  ngOnInit(): void {
    // generate random values for mainChart
    this.leafletMap();
    this.countUserAdmin();
    this.countUserByUser();
    this.countStationHydrology();
    this.countStationSynoptic();

  }
  ngOnDestroy() {
    // this.map.remove();
  }
  userlist() {
    console.log('testt');

    this.router.navigate(['/usermanager'])
  }


  async getstationListall() {
    await this.stationservice.getStationAll('').subscribe
      (res => {
        this.stationList = res.data
        console.log('list all station', this.stationList);


        for (let index = 0; index < this.stationList.length; index++) {
          const element = this.stationList[index];

          console.log('myLatssssssssss', element.lat);
          // console.log('myLongkkkkkkkkkkk', element.lng);


          let part = ''
          if (element.type == "Hydrology" || element.type == "Rain Gauge") {


            part = 'assets/img/water2.png'


          } else if (element.type == "CLIMATE" || element.type == "SYNOPTIC") {
            part = 'assets/img/skydrive.png'



          }

          // console.log(part, 'part', element.type);


          const icons = Leaflet.icon({
            iconUrl: part,
            iconSize: [24, 24],
            iconAnchor: [6, 23],
            popupAnchor: [0, -20],
          });

          // let html_str = `

          // <a onclick=" window.location.href = 'http://localhost:4200/#/stationbyone?stationId=${element.id}'; "> ssss</a>

          // `

          Leaflet.marker([element.lat, element.lng], {
            // .bindPopup(html_str)
            icon: icons, draggable: false, title: 'test'
          }).addTo(this.map).openTooltip().setTooltipContent('test').bindTooltip(element.name_lao, {
            permanent: false,
            direction: 'top'
          });

        }
      })
  }

  async leafletMap() {



    // Leaflet.marker([16.55444444, 104.7555556], {
    //   icon: customMarkerIcon, draggable: false, title: 'test'
    // }).addTo(this.map).openTooltip().setTooltipContent('test').bindTooltip('ສະຫວັນນະເຂດ	', {
    //   permanent: true,
    //   direction: 'bottom'
    // });


    navigator.geolocation.getCurrentPosition(resp => {
      console.log(resp);

      this.latitude = resp.coords.latitude;
      this.longitude = resp.coords.longitude;

      this.map = Leaflet.map('mapId').setView([this.latitude, this.longitude], 6);

      Leaflet.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        attribution: 'edupala.com ©️ Angular LeafLet',

      }

      ).addTo(this.map);
      const customMarkerIcon = Leaflet.icon({
        iconUrl: 'assets/img/custom-marker-icon.png',
        iconSize: [24, 24],
        iconAnchor: [6, 23],
        popupAnchor: [0, -20],
      }
      );




      let that = this;



      that.map.panTo(new Leaflet.LatLng(this.latitude, this.longitude));

      this.getstationListall()

    }


    ),
      error => {
        console.log('Error getting location', error);
      }

      ;
  }


  countUserAdmin() {
    const data = {
      role: 'admin'
    }

    this.authService.countUserByRole(data).subscribe
      (res => {
        console.log(res);
        if (res.status == '1') {

          this.adminCountAll = res.data[0]['user'];

        } else {
        }

      })


  }

  countUserByUser() {
    const data = {
      role: 'user'
    }
    console.log('pls give me data', data);

    this.authService.countUserByRole(data).subscribe
      (res => {
        console.log(res);
        if (res.status == '1') {

          this.userCountAll = res.data[0]['user'];

        } else {
        }

      })


  }

  countStationHydrology() {
    const data = {
      role: 'user'
    }
    console.log('pls give me data', data);

    this.stationservice.countHydrology(data).subscribe
      (res => {
        console.log(res);
        if (res.status == '1') {

          this.hydrologyCount = res.data[0]['total'];

        } else {
        }

      })


  }

  countStationSynoptic() {
    const data = {
      role: 'user'
    }
    console.log('pls give me data', data);

    this.stationservice.countSynoptic(data).subscribe
      (res => {
        console.log(res);
        if (res.status == '1') {

          this.synopticCount = res.data[0]['total'];

        } else {
        }

      })


  }
}
